import BackgroundImage from "gatsby-background-image";
import React from "react";

type BackgroundBlockProps = {
  backgroundImage: string;
  height: string;
  width: string;
};

export const BackgroundBlock = ({
  backgroundImage,
  height,
  width,
}: BackgroundBlockProps) => {
  return (
    <div
      className="background-wrapper"
      style={{ position: "absolute", top: 0, width, zIndex:-1 }}
    >
      <div
        style={{
          backgroundImage,
          height,
          width,
          transform: "translate(30px, -115px) rotate(-22deg)",
        }}
      ></div>
      <div
        style={{
          backgroundImage,
          height,
          width,
          transform: "translate(30px, 440px) rotate(14deg)",
        }}
      ></div>
      <div
        style={{
          backgroundImage,
          height,
          width,
          transform: "translate(0px, 950px) rotate(-32deg)",
        }}
      ></div>
    </div>
  );
};
