import React from "react";
import Img from "gatsby-image";
import "./ImageBlock.scss";
type ImageBlockProps = {
  align?: "left" | "right";
};
// Height of feature image should be 400px * mandatory => Use graphql for img height ?h=400
export const ImageBlock = ({ align }: ImageBlockProps) => {
  return (
    <div className="image-block-wrapper" img-align={align}>
      <img className="image" src="https://dummyimage.com/600x400/aaa/fff" />
      <div className="text">
        <div className="text__wrapper">
          <h2>Feature heading</h2>
          <p>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim
            ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut
            aliquip ex ea commodo consequat. Duis aute irure dolor in
            reprehenderit in voluptate.
          </p>
        </div>
      </div>
    </div>
  );
};
